import {
  createRouter,
  createWebHistory,
  Router
} from 'vue-router'


const Home = () => import('../views/Home/index.vue')
const Login = () => import('../views/Login/index.vue')
const Dashboard = () => import('../views/Dashboard/index.vue')
const Single = () => import('../views/Dashboard/Single.vue')
const CourseDashboard = () => import('../views/Dashboard/Course.vue')
const Courses = () => import('../views/Courses/index.vue')
const Course = () => import('../views/Courses/single.vue')
const Voucher = () => import('../views/Voucher/index.vue')
const Account = () => import('../views/Account/index.vue')
const Register = () => import('../views/Register/index.vue')
const ConfirmRegister = () => import('../views/Register/confirm.vue')
const ConfirmAccount = () => import('../views/Register/confirmAcc.vue')
const ResetPassword = () => import('../views/Register/reset-password.vue')
const Autologin = () => import('../views/Login/auto.vue')
const Cursos = () => import('../views/Search/index.vue')
const Pedidos = () => import('../views/MyAccount/myOrders.vue')

const routes = [
  {
    path: '/auto-login/:dados',
    name: 'Autologin',
    component: Autologin
  },
  {
    path: '/meus-pedidos',
    name: 'Pedidos',
    component: Pedidos
  },
  
  {
    path: '/store',
    name: 'Cursos',
    component: Cursos
  },
  
  {
    path: '/confirm-account/:status',
    name: 'ConfirmAccount',
    component: ConfirmAccount
  },
  {
    path: '/reset-password',
    name: 'ResetPassword',
    component: ResetPassword
  },
  {
    path: '/confirm-register',
    name: 'ConfirmRegister',
    component: ConfirmRegister
  },

  {
    path: '/lesson/:id/:key/:name?',
    name: 'Single',
    component: Single
  },
  {
    path: '/course-view/:id/:name?',
    name: 'CourseDashboard',
    component: CourseDashboard
  },

  {
    path: '/',
    name: 'Home',
    component: Home,
  },
  {
    path: '/account',
    name: 'Account',
    component: Account,
  },
  {
    path: '/courses',
    name: 'Courses',
    component: Courses,
  },
  {
    path: '/voucher',
    name: 'Voucher',
    component: Voucher,
  },

  {
    path: '/course',
    name: 'Course',
    component: Course,
  },
  {
    path: '/register/:nickname?',
    name: 'Register',
    component: Register
  },
  {
    path: '/login',
    name: 'Login',
    component: Login,
  },
  {
    path: '/dashboard',
    name: 'Dashboard',
    component: Dashboard,
  },


  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import( /* webpackChunkName: "about" */ '../views/About.vue')
  },
  {
    path: '/:pathMatch(.*)*',
    redirect: {
      name: 'Home'
    }
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router